import React, { useState } from 'react';
import PropTypes from 'prop-types';


const  Search  = (props)=> {
  const[text,setText] = useState('')
  
       const onSubmit = (e)=>{ 
       e.preventDefault()
   //console.log("the form has been submitted")
       if(text===''){
         props.alert('please enter your text','danger')
       }
       else{   
         props.searchmeals(text)
         setText(' ')
    } 
   }


const handlechange=(e)=>{
 setText(e.target.value)
}


const reset = ()=>{
      props.clearmeals()
}


    return(
     <div>
       <form onSubmit={onSubmit}>
      <input type="text" className="form-control" onChange={handlechange} placeholder="Input your text"/>
      <input type="submit" className="form-control"  value='submit' />
    </form>
      <button onClick={reset} className='btn btn-danger gitbutton'>Clear </button>
   </div>
   )
};


Search.propTypes = {
   searchmeals: PropTypes.func.isRequired,
   alert:PropTypes.func.isRequired,
   clearmeals:PropTypes.func.isRequired
 };


export default Search;
