import React from 'react';
import { Link } from 'react-router-dom';
const Mealitem = (props)=>  {
  //  console.log(this.props)
  
  return (
        <div className="card text-center">
          <img src ={props.user.strMealThumb}  alt=""  className="round-img"  style={{width:'60px'}}></img>         
          <div>
         
           <Link to={`/meal/${props.user.idMeal}`}  className="btn btn-primary btn-sm">Click Me</Link>
         
        </div>
       </div>      
      )
  }

export default Mealitem;
