import React, {Fragment } from 'react'
import {useState} from 'react'
import './App.css';
import Navbarapp from './components/layout/Appnavbar'
import About from './components/pages/About'
import { createBrowserHistory } from 'history';
import {Router, Route, Switch } from "react-router-dom";
import Meals from './components/meal/Meals'
import axios from 'axios'
import Search from './components/meal/Search'
import Meal from './components/meal/Meal'
import Alert from './components/layout/Alert'


const App =()=> {
const [meals, setMeals] = useState([]);
const [meal,  setMeal] = useState({});
const [myalert, setAlert] = useState(null);
const [loading,setLoading] =useState(false)

const searchmeals = async(text) =>{
  setLoading(true)
   const res =  await axios.get(`https://www.themealdb.com/api/json/v1/1/search.php?s=${text}`)   
   setMeals( res.data.meals) 
   setLoading(false)
}

const alert=(msg,type)=>{
      setAlert({msg,type})
      setTimeout(()=>setAlert(null),3000)  // setMsg and setType
}

const clearmeals = ()=>{
     setMeals([])
 }

const getmeal = async(id) =>{
   setLoading(true)
  const res =  await axios.get(`https://www.themealdb.com/api/json/v1/1/lookup.php?i=${id}`)   
  console.log(res)
  setMeal(res.data.meals[0]) 
  setLoading(false)
  console.log(meal)
 
}

const history = createBrowserHistory();
  return (
    <Router history={history}>
    <div className="App">
     <Navbarapp></Navbarapp> 
     <section className ="container">
      <Switch>
        <Route exact path='/' render={props=>(<Fragment>
        <Search searchmeals={searchmeals} clearmeals={clearmeals}  alert={alert}></Search> 
        <Alert alert={myalert}></Alert>
        <Meals meals={meals} loading={loading}></Meals>
        </Fragment>)}
     ></Route> 
      <Route exact path = "/about" component   = {About} />
      <Route exact path = '/meal/:idMeal' render={props=>(<Fragment>
           <Meal  {...props} getmeal={getmeal} loading={loading} meal={meal}></Meal>
      </Fragment>)}></Route>
    </Switch>
    </section>
 </div>
 </Router>

   );
}

export default App;
