import React, {Fragment } from 'react';
import Spinner from '../layout/Spiner'
import  { useEffect } from 'react'; 
import { Link } from 'react-router-dom';
  const Meal = (props)=>  {
  //  console.log(this.props)
 
  useEffect(() => { 
       props.getmeal(props.match.params.idMeal)
    },[])

      if(props.loading){
              return(<Spinner></Spinner>)
         }
      else{
        return( 
       <Fragment>
             <Link to = '/' className="btn btn-primary btn-sm" style={button} >Back to Search </Link>     
            
             <div className='flex'>
               <div className='all-center'>
                  <img src = {props.meal.strMealThumb} className='round-img' style={{width:'60px'}}></img>
                  <h1>{props.meal.strMeal}</h1>
                  <p>{props.meal.strArea} </p>
              </div>
              <div> 
              {props.meal.strInstructions && (
                         <Fragment>
                               <h3>Instructions</h3>
                               <p> {props.meal.strInstructions}</p>
                          </Fragment>  
                     )}        
              </div>   
             </div>
          </Fragment>)   
   }
}

const button = {

   width: '74px',
   height: '25px',
   fontSize: '10px'
}




export default Meal;
